import React, { Component } from 'react'
import { nanoid } from 'nanoid'
import '../Home.css'

class Withdraw extends Component {
  constructor(props) {
    super(props)
    this.state = {
      note: '',
      recipientAddr: '',
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  handleTok(e) {
    let { innerText } = e.target
    this.props.tokSelected(innerText)
  }
  handleChange(e) {
    this.setState({[e.target.name]: e.target.value})
  }
  handleSubmit(e) {
    e.preventDefault()
  }
  render() {
    const { recipientAddr, note } = this.state
    return (
      <form
        onSubmit={this.handleSubmit}
        style={{
          display: 'flex', 
          flexDirection: 'column',
        }}>
        <div>Note</div>
        <input
          type={'text'}
          name={'note'}
          value={note}
          onChange={this.handleChange}/>
        <div>
          <div>Recipient Address</div>
          <div>Donate</div>
        </div>
        <input
          type={'text'}
          name={'recipientAddr'}
          value={recipientAddr}
          onChange={this.handleChange}/>
        <input
          type={'submit'}
          value={'Withdraw'}/>
      </form>
    )
  }
}

export default Withdraw